<template>
  <div class="login-container" :style="`min-height:${scrollHeight}px;`">
    <div style="text-align: center; padding-top: 120px;">
      <img :src="logo" style="width: 215px; height: 55px;border-radius: 10px;">
    </div>
    <div style="padding: 30px;">
      <van-form @submit="onSubmit" :show-error-message="false" label-width="50" >
        <van-field v-model.trim="username" name="username" size="large" placeholder="请输入手机号" :rules="[{ required: true, message: '请输入手机号' }]">
          <template #left-icon>
            <van-icon name="contact-o" size="20" style="margin-right: 10px;" />
          </template>
        </van-field>
        <div style="margin-top: 20px;"/>
        <van-field v-model.trim="password" :type="passwordType" name="password" size="large" placeholder="请输入密码" :rules="[{ required: true, message: '请输入密码' }]">
          <template #left-icon>
            <van-icon :name="require('@/assets/icon/password.png')" size="20" style="margin-right: 10px;" />
          </template>
          <template slot="right-icon">
            <span @click="switchPasswordType">
              <van-icon v-if="passwordType === 'password'" name="closed-eye" />
              <van-icon v-else name="eye" />
            </span>
          </template>
        </van-field>
        <div style="margin: 20px;">
          <van-button round block type="info" native-type="submit">登录</van-button>
        </div>
      </van-form>
      <p style="font-size: 14px; color: #5177e4;">
<!--        <span style="float: left; cursor: pointer;">验证码登录</span>-->
<!--        <span style="float: right; cursor: pointer;">忘记密码</span>-->
      </p>
    </div>
  </div>
</template>

<script>

export default {
  name: 'LoginView',
  data() {
    return {
      username: '',
      password: '',
      scrollHeight: window.innerHeight,
      logo: require('@/assets/logo.png'),
      passwordType: 'password',
      language: this.$store.getters.getLanguage
    }
  },
  mounted() {
  },
  methods: {
    async onSubmit(form) {
      const params = {
        phone: form.username,
        password: form.password,
        lang: this.language
      }
      const res = await this.$post('/login', params)
      this.$common.setToken(res.token)
      // 初始化数据
      await this.$store.dispatch('setUserInfo', res.user)
      await this.$store.dispatch('setCurrentProject', res.currentProject)
      await this.$store.dispatch('setListProject', res.listProject)
      await this.$router.replace('/ticket')
    },
    switchPasswordType() {
      this.passwordType = this.passwordType === 'password' ? 'text' : 'password'
    }
  }
}
</script>

<style scoped>
.login-container {
  width: 100%;
  height: 100%;
  background-color: white;
}
</style>
